@use 'variables' as *;

.title {
    padding-left: 10px;
    margin-bottom: 5px;
    font-style: normal;
    font-weight: 600;
    font-size: $D_majorHeadingFontSize;
    color: rgba(255, 255, 255, 0.5);
}
.listing {
    background: rgba(196, 196, 196, 0.3);
    border-radius: 15px;
    padding: 10px;
    margin: 15px 0;
    display: flex;
    flex-wrap: wrap;
    overflow-y: scroll;
    padding: 10px;
    justify-content: space-evenly;
    &::-webkit-scrollbar {
        display: none;
    }
}

.poster {
    padding: 10px;
}

.img {
    border-radius: 5px;
    width: 150px;
    height: 210px;
    object-fit: cover;
    transition: transform 400ms;
    &:hover {
        cursor: pointer;
        transform: scale(1.1);
    }
}
